import React, { FC } from 'react';
import { Avatar, makeStyles } from '@material-ui/core';
import { Icon } from '../model/Responses';
import ShoppingBasketIcon from '@material-ui/icons/ShoppingBasket';
import HomeIcon from '@material-ui/icons/Home';
import HomeWorkIcon from '@material-ui/icons/HomeWork';
import DriveEtaIcon from '@material-ui/icons/DriveEta';
import WbSunnyIcon from '@material-ui/icons/WbSunny';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import RedeemIcon from '@material-ui/icons/Redeem';
import SchoolIcon from '@material-ui/icons/School';
import { ReceiptOutlined } from '@material-ui/icons';

const useStyles = makeStyles(() => ({
  icon: {
    marginRight: '1em',
    width: '30px',
    height: '30px',
  },
}));

const icons = {
  Receipt: ReceiptOutlined,
  ShoppingBasket: ShoppingBasketIcon,
  Home: HomeIcon,
  HomeWork: HomeWorkIcon,
  DriveEta: DriveEtaIcon,
  WbSunny: WbSunnyIcon,
  MonetizationOn: MonetizationOnIcon,
  Redeem: RedeemIcon,
  School: SchoolIcon,
};

const iconsDict: Record<string, React.ComponentType> = icons;

export type IconName = keyof typeof icons;

interface BucketIconProps {
  title: string;
  icon: Icon;
  colored?: boolean;
  className?: string;
  style?: React.CSSProperties;
}

const TextIcon: FC<BucketIconProps> = ({ title, icon, ...props }) => {
  return (
    <Avatar alt={title} title={title} {...props}>
      {icon.name}
    </Avatar>
  );
};

const PicIcon: FC<BucketIconProps> = ({ icon, ...props }) => {
  const ChosenIcon = iconsDict[icon.name];
  return <ChosenIcon {...props} />;
};

export const BucketIcon: FC<BucketIconProps> = props => {
  const classes = useStyles();
  const style = props.colored && props.icon.color ? { backgroundColor: props.icon.color } : {};

  if (props.icon.type === 'AVATAR') {
    return <TextIcon className={classes.icon} style={style} {...props} />;
  } else {
    return <PicIcon className={classes.icon} style={style} {...props} />;
  }
};
