import React from 'react';
import HistoryRepository from './HistoryRepository';
import { Divider, Grid, List, ListItem, ListSubheader, makeStyles, Paper, Typography } from '@material-ui/core';
import { BucketIcon } from '../buckets/BucketIcon';
import Timeline from '@material-ui/lab/Timeline';
import {
  TimelineConnector,
  TimelineContent,
  TimelineDot,
  TimelineItem,
  TimelineOppositeContent,
  TimelineSeparator,
} from '@material-ui/lab';
import { AddCircle, RemoveCircle } from '@material-ui/icons';
import TimerIcon from '@material-ui/icons/Timer';
import { MoneyText } from '../components/MoneyText';

const useStyle = makeStyles(theme => ({
  container: {
    margin: theme.spacing(0, 0, 1, 1),
    width: `calc(100% - ${theme.spacing(2)}px)`,
  },
  subheader: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(1, 0),
  },
  listItem: {
    padding: theme.spacing(0),
  },
  operationTitle: {
    textTransform: 'capitalize',
  },
}));

export const History: React.FC = () => {
  const classes = useStyle();

  const history = HistoryRepository.findAll();

  return (
    <Grid container direction={'column'} className={classes.container} spacing={1}>
      {history.map(entry => (
        <Grid key={entry.bucket.id} item>
          <Paper elevation={2}>
            <List>
              <ListSubheader className={classes.listItem}>
                <Grid container item direction={'row'} justify={'center'} className={classes.subheader}>
                  <BucketIcon title={entry.bucket.name} icon={entry.bucket.icon} />
                  <Typography variant="subtitle1" color="textSecondary">
                    {entry.bucket.name}
                  </Typography>
                </Grid>
                <Divider />
              </ListSubheader>
              <ListItem className={classes.listItem}>
                <Timeline>
                  {/*TODO: Wydzielić mini component??*/}
                  <TimelineItem>
                    <TimelineOppositeContent>
                      <Typography variant="body2" color="textSecondary">
                        teraz
                      </Typography>
                    </TimelineOppositeContent>
                    <TimelineSeparator>
                      <TimelineDot>
                        <TimerIcon color={'disabled'} />
                      </TimelineDot>
                      <TimelineConnector />
                    </TimelineSeparator>
                    <TimelineContent />
                  </TimelineItem>
                  {entry.operations.map(operation => (
                    <TimelineItem>
                      <TimelineOppositeContent>
                        <Typography variant="body2" color="textSecondary">
                          {new Date(operation.date).toLocaleString()}
                        </Typography>
                      </TimelineOppositeContent>
                      <TimelineSeparator>
                        <TimelineDot color={operation.operation === 'add' ? 'primary' : 'secondary'}>
                          {operation.operation === 'add' ? <AddCircle /> : <RemoveCircle />}
                        </TimelineDot>
                        <TimelineConnector />
                      </TimelineSeparator>
                      <TimelineContent>
                        <Typography variant="body1">
                          <MoneyText value={operation.amount} />
                        </Typography>
                        <Typography variant="body2" className={classes.operationTitle}>
                          {operation.title}
                        </Typography>
                      </TimelineContent>
                    </TimelineItem>
                  ))}
                </Timeline>
              </ListItem>
            </List>
          </Paper>
        </Grid>
      ))}
    </Grid>
  );
};
