function calculateValue(buckets: BucketResponse[]): number {
  return buckets.map(b => b.amount.value).reduce((sum, current) => sum + current);
}

const allBuckets = (): BucketResponse[] => _.cloneDeep(buckets);

const bucketById = (id: string): BucketResponse => {
  const bucket = buckets.find(b => b.id === id);
  if (bucket) {
    return { ...bucket };
  } else {
    throw 'Cannot find bucket';
  }
};

const saveBucket = (bucket: BucketResponse): BucketResponse => {
  const index = buckets.findIndex(b => b.id === bucket.id);
  buckets[index] = { ...bucket };
  return { ...buckets[index] };
};

const allAccounts = (): AccountResponse[] => _.cloneDeep(accounts);

const historyByBucketId = (id: string): HistoryResponse => {
  const bucketHistory = history.find(h => h.bucket.id === id);
  if (bucketHistory) {
    return _.cloneDeep(bucketHistory);
  } else {
    const bucket = bucketById(id);
    return {
      bucket,
      operations: [],
    };
  }
};

const allHistory = (): HistoryResponse[] => {
  return allBuckets()
    .map(b => b.id)
    .map(historyByBucketId);
};

export default {
  allBuckets,
  bucketById,
  saveBucket,
  allAccounts,
  allHistory,
  historyByBucketId,
};
