import DataSource from '../mock/DataSource';
import { BucketResponse } from '../model/Responses';

class BucketsRepository {
  findAll(): readonly BucketResponse[] {
    return DataSource.allBuckets();
  }

  getById(id: string) {
    return DataSource.bucketById(id);
  }

  save(bucket: BucketResponse): BucketResponse {
    return DataSource.saveBucket(bucket);
  }

  add(id: string, amount: number): BucketResponse {
    const bucket = DataSource.bucketById(id);
    bucket.amount.value += amount;
    DataSource.saveBucket(bucket);
    return DataSource.bucketById(id);
  }

  withdraw(id: string, amount: number): BucketResponse {
    const bucket = DataSource.bucketById(id);
    bucket.amount.value -= amount;
    DataSource.saveBucket(bucket);
    return DataSource.bucketById(id);
  }
}

const BucketsRepositoryInstance = new BucketsRepository();

export { BucketsRepositoryInstance as BucketsRepository };
